import config from "../config.json";
import React from "react";
import SurveyStatement from "./SurveyStatement";

export default function SurveyStatementsWrapper(props) {
  let chosenStatementIds = props.chosenStatementIds.chosenStatementIds;

  return config.survey_statements.map((statement) => {
    return (
      <SurveyStatement
        active={chosenStatementIds.includes(statement.id.toString())}
        key={statement.id}
        statement={statement}
      ></SurveyStatement>
    );
  });
}
