import React from "react";
import { useParams } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { Link } from "react-router-dom";
import config from "../config.json";

export default function PlatformInfoPage() {
  let params = useParams();
  let platform = config.platforms.find((platform) => (platform.id = params.id));
  return (
    <Container className="platform-info slide-in-blurred-top my-5">
      <Row>
        <Col className="p-3" sm={12} md={12}>
          <img
            src={`${process.env.PUBLIC_URL}/images/${platform.img_name}`}
            alt={platform.display_name}
          ></img>
          <h1 className="d-inline-block">{platform.display_name}</h1>

          <div className="float-end">
            <Link to="/survey" className="d-inline-block btn btn-primary mx-2">
              Go back
            </Link>
            <a
              className="d-inline-block btn btn-dark mx-2"
              href={platform.link}
            >
              Get Started Using {platform.display_name}
            </a>
          </div>
        </Col>
        <hr />
        <Col className="answer p-3" sm={12} md={4}>
          <h2>This platform is great for:</h2>
          <p>{platform.great_for.join(", ")}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>It's primarily used by:</h2>
          <p>{platform.primary_users.join(", ")}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>It's best for sharing content that needs to be accessed by:</h2>
          <p>{platform.who_needs_access.join(", ")}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>Can I control who sees my content with this platform?</h2>
          <p>{platform.allows_access_control}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>Does it allow/promote conversation and communication?</h2>
          <p>{platform.allows_conversation_and_communication}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>Is it open to Search Engines/Google?</h2>
          <p>{platform.open_to_search_engines}</p>
        </Col>
        <Col className="answer p-3" sm={12} md={4}>
          <h2>Can you use it to store sensitive data?</h2>
          <p>{platform.can_store_sensitive_data}</p>
        </Col>
      </Row>
    </Container>
  );
}
