import React from "react";
import Container from "react-bootstrap/Container";
import SurveyStatementsWrapper from "./SurveyStatementsWrapper";

export default function Survey(chosenStatementIds) {
  return (
    <Container
      fluid
      id="survey"
      className="survey m-3-auto my-3 p-3 text-center"
    >
      <h2>Choose the options that apply to you and your content</h2>
      <hr />
      <SurveyStatementsWrapper
        chosenStatementIds={chosenStatementIds}
      ></SurveyStatementsWrapper>
    </Container>
  );
}
