import React from "react";
import Button from "react-bootstrap/Button";
import { useSearchParams } from "react-router-dom";
import { Link } from "react-router-dom";
export default function SurveyStatement(props) {
  let className = props.active
    ? "p-3 m-3 rounded survey-statement btn btn-primary active"
    : "p-3 m-3 rounded survey-statement btn btn-primary";
  // get the current queryParam value for ?statements=<>
  let [searchParams, setSearchParams] = useSearchParams();
  let currentChosenStatements = searchParams.get("statements");
  let currentChosenStatementIds;
  if (!currentChosenStatements) {
    currentChosenStatementIds = [];
  } else {
    currentChosenStatementIds = currentChosenStatements.split("--");
  }
  // target of this link depends on if this statement is currently active
  // if active, set new target to be currentParamValue without the current statment id
  // else, add statement id to currentParamValue
  if (props.active) {
    let indexOfThisStatementId = currentChosenStatementIds.indexOf(
      props.statement.id.toString()
    );
    if (indexOfThisStatementId > -1) {
      currentChosenStatementIds.splice(indexOfThisStatementId, 1); // remove this item
    }
  } else {
    currentChosenStatementIds.push(props.statement.id.toString());
  }
  let onClickHref = `/survey/?statements=${currentChosenStatementIds.join(
    "--"
  )}`;

  return (
    <Link to={onClickHref} className={className} id={props.statement.id}>
      {props.statement.text}
    </Link>
  );
}
