import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import { Link } from "react-router-dom";
class Platform extends React.Component {
  render() {
    return (
      <Col
        data-relevant-survey-statements={this.props.platform.relevant_survey_statements.join(
          "--"
        )}
        className="platform-container"
        sm={12}
        md={2}
      >
        <Card className="platform">
          <Card.Img
            variant="top"
            src={`${process.env.PUBLIC_URL}/images/${this.props.platform.img_name}`}
          />
          <Card.Body>
            <Card.Title>{this.props.platform.display_name}</Card.Title>
            <Card.Text className="description">
              {this.props.platform.description}
            </Card.Text>
            <Button
              target="_blank"
              className="mx-3"
              href={this.props.platform.link}
            >
              Get Started
            </Button>
            <Link
              className="mx-3 btn btn-primary"
              role="button"
              to={`/platform/${this.props.platform.id}`}
            >
              More Info
            </Link>
          </Card.Body>
        </Card>
      </Col>
    );
  }
}

export default Platform;
