import React from "react";
import Platform from "./Platform";
import config from "../config.json";
export default function PlatformList(chosenStatementIds) {
  // function to check if all elements of one array are in target array
  let checker = (arr, target) => target.every((v) => arr.includes(parseInt(v)));
  return config.platforms.map((platform, index) => {
    if (
      checker(
        platform.relevant_survey_statements,
        chosenStatementIds.chosenStatementIds
      )
    ) {
      return <Platform key={index} platform={platform}></Platform>;
    }
  });
}
