import React from "react";
import Container from "react-bootstrap/Container";

class Intro extends React.Component {
  render() {
    return (
      <Container className="p-3 m-3-auto my-3 intro slide-in-blurred-top">
        <h1>Welcome to {this.props.app_title}</h1>
        <p>
          So, you're looking to <b>share</b> something. Luckily, College of
          Charleston members have access to a ton of tools for digitally sharing
          information. But how do you find the <b>right tool?</b>
        </p>
        <hr />
        <p>
          If you're reading this,<b> you've arrived at your answer.</b> CofC
          DigiKit will make the process of identifying the right tool for
          sharing your information
          <b> super straightforward</b>. Just answer some questions about what
          you're trying to share, and we'll do the work for you.
        </p>
        <hr />
      </Container>
    );
  }
}
export default Intro;
